* {
  font-family: "Poppins", sans-serif;
  box-sizing: border-box;
}

body {
  background-color: white;
  color: black;
  text-align: center;
  align-items: center;
  justify-content: center;
  height: 100%;
  margin: 0;
  flex-direction: column;
}

h1 {
  font-weight: 250;
}

h2 {
  font-weight: 200;
}

h4 {
  font-weight: 300;
}

p {
  font-weight: 150;
}

a {
  color: white;
  text-decoration: none;
  cursor: pointer;
}

img {
  border-radius: 5px;
}

.letsgopadding {
  padding: 10px;
}

.big-text {
  font-size: 2.8em;
}

::selection {
