.select-container {
  justify-content: center;
  align-items: center;
  text-align: center;
}

select {
  margin: 0 auto;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  outline: 0;
  box-shadow: none;
  border: 0 !important;
  background: #f0e395;
  background-image: none;
  color: white;
  font-size: 25px;
}

select::-ms-expand {
  display: none;
}

.select {
  margin: 0 auto;
  position: relative;
  display: flex;
  width: 20em;
  height: 3em;
  line-height: 3;
  background: #f0e395;
  overflow: hidden;
  border-radius: 0.25em;
  color: white;
}

select {
  flex: 1 1;
  padding: 0 0.5em;
  color: #fff;
  cursor: pointer;
}

.select::after {
  content: "\25BC";
  position: absolute;
  top: 0;
  right: 0;
  padding: 0 1em;
  background: #ffb485;
  cursor: pointer;
  pointer-events: none;
  transition: 0.25s all ease;
}

.clear-cart-btn button {
  background: #ffb485;
  cursor: pointer;
  border: none;
  margin: 0 auto;
  color: white;
  padding: 7px;
  border-radius: 5px;
  font-size: 20px;
}

.buy-product button {
  background: #ffb485;
  cursor: pointer;
  border: none;
  color: white;
  padding: 7px;
  border-radius: 5px;
  font-size: 20px;
  width: 80px;
  text-transform: uppercase;
  letter-spacing: 2px;
}

