}

footer {
  padding: 10px;
  background-color: #d15c95;
  color: white;
}

footer a {
  text-decoration: line-through;
}

.footer-content {
  min-height: calc(100vh - 70px);
}

.wrapper {
  margin: 0 auto;
  width: 90%;
}

.cards {
  list-style: none;
  margin: 0;
  padding: 0px;
}

.cards li {
  border: 2px solid #ffb485;
  border-radius: 5px;
  background-color: #e8e8e8;
  color: #fff;
  flex: 1 1 200px;
}

.price-colour {
  color: black;
}

.cards h2 {
  background-color: #ffb485;
  margin: 0;
  padding: 10px;
}

.cards p {
  padding: 10px;
}

.flex {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -10px;
}

.flex li {
  flex: 1 1 200px;
  margin: 10px;
}

